	.qode_product_list_pinterest_holder,
	.full_width .grid_section .section_inner .qode_product_list_pinterest_holder,
    .qode_product_list_masonry_holder,
    .full_width .grid_section .section_inner .qode_product_list_masonry_holder {

        $gutter_space: 3.90625% - 0.0001%; // 30px
        $masonry_list_columns: ('two', 'three', 'four');

        .qode_product_list_gutter {
            width: $gutter_space;
        }

        @for $i from 0 to length($masonry_list_columns) {
            &.#{nth($masonry_list_columns,$i+1)}_columns {
                $column_width: (100% - $gutter_space)/2;
                @if ($i+2) != 2 {
                    $column_width: (100% - ($gutter_space * 2))/3;
                }

                .qode_product_list_item,
                .qode_product_list_sizer{
                    width: $column_width;
                }

                .qode_product_list_item.large-width-height{
                    width: $column_width*2+$gutter_space;
                }
            }
        }
    }

    .full_width .qode_product_list_pinterest_holder,
    .full_width .qode_product_list_masonry_holder {

        $gutter_space: 2.9296875% - 0.0001%; // 30px
        $masonry_list_columns: ('two', 'three', 'four');

        .qode_product_list_gutter {
            width: $gutter_space;
        }

        @for $i from 0 to length($masonry_list_columns) {
            &.#{nth($masonry_list_columns,$i+1)}_columns {
                $column_width: (100% - $gutter_space)/2;
                @if ($i+2) != 2 {
                    $column_width: (100% - ($gutter_space * 2))/3;
                }

                .qode_product_list_item,
                .qode_product_list_sizer{
                    width: $column_width;
                }

                .qode_product_list_item.large-width-height{
                    width: $column_width*2+$gutter_space;
                }
            }
        }
    }
}

