//WooCommerce Cart Responsiveness - start
@include phone-portrait{
    .woocommerce-page.woocommerce-cart{
        .woocommerce{
            table.cart{
                display: block;
                text-align: center;

                thead{
                    display: none;
                }

                tbody{
                    display: block;

                    tr{
                        display: block;
                        width: 100%;
                        padding: 10px 0;

                        &:not(:nth-last-child(2)){
                            border-bottom: none;
                        }
                    }

                    td{
                        display: block;
                        text-align: center;

                        img{
                            margin: 0 auto;
                        }
                    }
                }
            }
        }
    }
}
//WooCommerce Cart Responsiveness - end

